import React from 'react';
import './App.css';


const person = ( cont ) => {
return (
    <div className="per">
     <img src={cont.src} className="m-auto" alt={cont.name} />
     <div>
        <h6>{cont.name}</h6>
        <p>UGX {cont.amount} <sup><del>UGX {cont.amount}</del></sup></p>

        <button><i className="fas fa-plus mr-1"></i> Add</button>
    </div>
    </div>
);
}

export default person;